.AboutMe {
    background-image: linear-gradient(rgba(0, 0, 0, .5), rgba(0, 0, 0, .7)), url('https://wallpapercave.com/wp/wp4974496.jpg');
    height: 100vh;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    position: relative;
}

.AboutMe-content {
    border: 1px solid white;
    border-radius: .4rem;
    -webkit-border-radius: .4rem;
    -moz-border-radius: .4rem;
    -ms-border-radius: .4rem;
    -o-border-radius: .4rem;
    margin-top: 5rem;
    padding: 2rem;
    color: white;
    margin-left: 19rem;
    margin-right: 19rem;
    padding-left: 4rem;
    padding-right: 4rem;
    background-color: white;
    background: rgba(0, 0, 0, 0.4); 
    -webkit-box-shadow:2px 4px 8px rgba(0, 0, 0, 0.5), 10px 20px 40px rgba(0, 0, 0, 0.3) inset;
    -moz-box-shadow:2px 4px 8px rgba(0, 0, 0, 0.5), 10px 20px 40px rgba(0, 0, 0, 0.3) inset;
    box-shadow:2px 4px 8px rgba(0, 0, 0, 0.5), 10px 20px 40px rgba(0, 0, 0, 0.3) inset;
}

.text {
    text-align: start;
}

.link:hover {
    text-decoration: none;
}